import uniPopup from "../../../components/uni-popup/uni-popup.vue"
import {
	CONTACTDETAIL,
	DELCONTACT,
	ADDBLACKLIST,
	GETPROLPGUE,
	CANCELPROLPGUE,
	AUDITION
} from "../../../utils/api.js"
let innerAudioContext = uni.createInnerAudioContext();
export default {
	components: {
		uniPopup
	},
	data() {
		return {
			mobile: "",
			detailData: "",
			id: "",
			head: "",
			delShow: false,
			name: "",
			refuseShow: false,
			ProlgueData: '',
			textShow: false,
			recordUrl: ''
		}
	},
	computed: {
		changeTime: function() {
			var time = this.detailData.inboundDuration
			var result = 0
			if (time >= 3600 * 24) {
				result = time / 3600 / 24 + ',天'
			} else if (time >= 3600 && time <= 3600 * 24) {
				result = time / 3600 + ',小时'
			} else if (time >= 60 && time < 3600) {
				result = time / 60 + ',分'
			} else {
				result = time + ',秒'
			}
			return result
		}
	},
	filters: {
		fomateMobile: function(value) {
			var pat = /(\d{3})\d*(\d{4})/
			var b = value.replace(pat, '$1****$2');
			return b
		}
	},
	onShow() {
		if (this.mobile) {
			this.initData()
			// 查询自定义开场白
			this.getProlgue()
		}
	},
	onHide() {
		innerAudioContext.stop()
		innerAudioContext.onStop(function() {
			console.log('暂停')
		})
	},
	onLoad(options) {
		uni.showToast({
			title: '加载中...',
			icon: 'none',
			mask: true
		})
		var getdata = JSON.parse(options.detail)
		this.mobile = getdata.mobile
		this.head = getdata.head
		this.initData()
		// 查询自定义开场白
		this.getProlgue()
	},
	onPullDownRefresh() {
		this.initData()
		this.getProlgue()
	},
	methods: {
		// 初始化数据
		initData() {
			const params = {
				mobile: this.mobile
			}
			this.$request.url_request(CONTACTDETAIL, params, "GET", res => {
				this.detailData = JSON.parse(res.data).data
				this.id = JSON.parse(res.data).data.id
				this.name = JSON.parse(res.data).data.name
				uni.stopPullDownRefresh()
				uni.hideToast()
			}, err => {})
		},
		// 打电话
		call() {
			if (this.mobile == '057126211779') {
				uni.showModal({
					content: '当前用户为示例用户，请添加自己的联系人再拨打',
					showCancel: false
				});
			} else {
				uni.makePhoneCall({
					phoneNumber: this.mobile
				})
			}

		},
		// 获取自定义开场白
		getProlgue() {
			const params = {
				mobile: this.mobile
			}
			this.$request.url_request(GETPROLPGUE, params, 'GET', res => {
				this.ProlgueData = JSON.parse(res.data).data
				if (this.ProlgueData) {
					if (this.ProlgueData.customOssUrl || this.ProlgueData.customText) {
						this.textShow = true
						this.recordUrl = this.ProlgueData.customOssUrl
						this.$forceUpdate()
					}
				}

				uni.stopPullDownRefresh()
			}, err => {})
		},
		// 播放试听
		playUrl() {
			console.log(this.ProlgueData.type)
			if (this.ProlgueData.type == 1) {
				if (this.ProlgueData.customText) {
					const params = {
						text: this.ProlgueData.customText
					}
					this.$request.url_request(AUDITION, params, "POST", res => {
						this.recordUrl = JSON.parse(res.data).data.ossUrl
						innerAudioContext.src = this.recordUrl
						innerAudioContext.autoplay = true
						innerAudioContext.play()
						innerAudioContext.onPlay(function() {
							console.log('正在试听')
						})
					}, err => {})
				}

			} else if (this.ProlgueData.type == 2) {
				this.recordUrl = this.ProlgueData.customOssUrl
				innerAudioContext.src = this.recordUrl
				innerAudioContext.autoplay = true
				innerAudioContext.play()
				innerAudioContext.onPlay(function() {
					console.log('开始播放')
				})
			}
		},
		//删除录音
		delText() {
			var _this = this
			uni.showModal({
				title: '提示',
				content: '确认删除开场白？',
				duration: 5000,
				success: function(res) {
					if (res.confirm) {
						const params = {
							mobile: _this.mobile,
						}
						_this.$request.url_request(CANCELPROLPGUE, params, 'GET', res => {
							uni.showToast({
								title: '删除成功',
								icon: 'none',
								mask: true,
								duration: 1200
							});
							_this.textShow = false
						}, err => {})
					} else if (res.cancel) {}

				}
			})
		},
		// 录制开场白
		editRecord() {
			var data = JSON.stringify({
				mobile: this.mobile
			})
			if (this.ProlgueData !== null) {
				if (this.ProlgueData.customText !== null || this.ProlgueData.customText !== '') {
					data = JSON.stringify({
						mobile: this.mobile,
						text: this.ProlgueData.customText
					})
				}
			}
			console.log(data)
			uni.navigateTo({
				url: "../editOpen/editOpen?data=" + data
			})
		},
		// 添加联系人
		addUser() {
			uni.navigateTo({
				url: '../AddContact/AddContact?mobile=' + this.mobile
			})
		},
		// 删除联系人
		delContact() {
			var _this = this
			uni.showModal({
				title: '提示',
				content: '确认删除该联系人？',
				duration: 5000,
				success: function(res) {
					if (res.confirm) {
						const params = {
							contactsId: _this.id
						}
						_this.$request.url_request(DELCONTACT, params, "GET", res => {
							uni.showToast({
								title: "删除成功！",
								icon: "success",
								mask: true,
								duration: 500
							})
							setTimeout(function() {
								uni.hideToast()
								uni.navigateBack({

								})
							}, 500);
						}, err => {})
					} else if (res.cancel) {}

				}
			})
		},
		// 拒绝来电
		addBlacklist() {
			var _this = this
			uni.showModal({
				title: '提示',
				content: '确认加入黑名单？',
				duration: 5000,
				success: function(res) {
					if (res.confirm) {
						const params = {
							mobile: _this.mobile,
							name: _this.name
						}
						_this.$request.url_request(ADDBLACKLIST, params, "GET", res => {
							uni.showToast({
								title: "拒绝成功！",
								icon: 'success',
								duration: 1000
							})
							setTimeout(function() {
								uni.hideToast()
								uni.navigateBack({

								})
							}, 500);
						}, err => {})
					}
				}
			});
		},
		// 更多
		open() {
			this.$refs.popup.open()
		}
	}
}
